body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}

.header {
  padding: 10px;
  background-color: firebrick;
  color: #ffffff;
  position: fixed;
  top: 0;
  right:0;
  left: 0;
  z-index: 100;
}

.header h1 {
  font-size: 25px;
  padding: 0;
  margin: 0;
  text-align: center;
}

.map {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.location-icon {
  font-size: 2rem ;
  color:red;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;

}

.loader img {
  width: 100px;
}

.loader h1 {
  margin-top: -10px;;
}

.location-info {
  position:absolute;
  /* top:80px;
  right: 50px;
  width: 400px; */
  top: 90px;
  right: 4%;
  width: 35%;
  min-height: 200px;
  padding: 20px;
  background-color: #35221780;
  border-radius: 10px;
  font-size: 18px;
  color: #ddd0ce;
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
}